  animation: animloader 0.3s 0.3s linear infinite alternate;
  border-radius: 4px;
  height: 50px;
  width: 5px;
  transform: rotate(-45deg);
}

.loader::after,
.loader::before {
  content: "";
  width: 5px;
  height: 50px;
  border-radius: 4px;
  background: #01e193;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 20px;
  animation: animloader 0.3s 0.45s linear infinite alternate;
}

.loader::before {
  left: -20px;
  animation-delay: 0s;
}

